.prettyprint.language-js,
.prettyprint.language-html,
.prettyprint.language-css,
.prettyprint.language-scss,
.prettyprint.language-sass,
.prettyprint.language-less,
.prettyprint.language-cs,
.prettyprint:not([class*="language-"]) {
  // Customization on top of theme: lighten the line numbers and set the background
  color: @gray-light;
  background-color: white;
  
  /* Tomorrow Theme */
  /* Original theme - https://github.com/chriskempson/tomorrow-theme */
  /* Pretty printing styles. Used with prettify.js. */
  /* SPAN elements with the classes below are added by prettyprint. */
  /* plain text */
  .pln {
    color: #4d4d4c; }

  @media screen {
    /* string content */
    .str {
      color: darken(#5DB12E, 10%); }

    /* a keyword */
    .kwd {
      color: #8959a8; }

    /* a comment */
    .com {
      color: #8e908c; }

    /* a type name */
    .typ {
      color: #4271ae; }

    /* a literal value */
    .lit {
      color: #f5871f; }

    /* punctuation */
    .pun {
      color: #4d4d4c; }

    /* lisp open bracket */
    .opn {
      color: #4d4d4c; }

    /* lisp close bracket */
    .clo {
      color: #4d4d4c; }

    /* a markup tag name */
    .tag {
      color: #c82829; }

    /* a markup attribute name */
    .atn {
      color: #f5871f; }

    /* a markup attribute value */
    .atv {
      color: #3e999f; }

    /* a declaration */
    .dec {
      color: #f5871f; }

    /* a variable name */
    .var {
      color: #c82829; }

    /* a function name */
    .fun {
      color: #4271ae; } }
  /* Use higher contrast and text-weight for printable form. */
  @media print, projection {
    .str {
      color: #060; }

    .kwd {
      color: #006;
      font-weight: bold; }

    .com {
      color: #600;
      font-style: italic; }

    .typ {
      color: #404;
      font-weight: bold; }

    .lit {
      color: #044; }

    .pun, .opn, .clo {
      color: #440; }

    .tag {
      color: #006;
      font-weight: bold; }

    .atn {
      color: #404; }

    .atv {
      color: #060; } }
  /* Style */
  /*
  pre.prettyprint {
    background: white;
    font-family: Consolas, Monaco, 'Andale Mono', monospace;
    font-size: 12px;
    line-height: 1.5;
    border: 1px solid #ccc;
    padding: 10px; }
  */

  /* Specify class=linenums on a pre to get line numbering */
  ol.linenums {
    margin-top: 0;
    margin-bottom: 0; }

  /* IE indents via margin-left */
  li.L0,
  li.L1,
  li.L2,
  li.L3,
  li.L4,
  li.L5,
  li.L6,
  li.L7,
  li.L8,
  li.L9 {
    /* */ }

  /* Alternate shading for lines */
  li.L1,
  li.L3,
  li.L5,
  li.L7,
  li.L9 {
    /* */ }
}